
















import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';

@Component
export default class CSelectorHMS extends Vue {
  // Props
  @Prop() public pointHour!: number;
  @Prop() public pointMinute!: number;
  @Prop() public pointSecond!: number;

  // Data
  private dataHours: number[] = [];
  private dataMinutesAndSeconds: number[] = [];
  private dataPointHour: number = this.pointHour;
  private dataPointMinute: number = this.pointMinute;
  private dataPointSecond: number = this.pointSecond;

  // Lifecycle hook
  created(): void {
    this.initHours();
    this.initMinutesAndSeconds();
  }

  // Emit
  @Emit('selectHour')
  private emitSelectHour(): number {
    return this.dataPointHour;
  }
  @Emit('selectMinute')
  private emitSelectMinute(): number {
    return this.dataPointMinute;
  }
  @Emit('selectSecond')
  private emitSelectSecond(): number {
    return this.dataPointSecond;
  }

  // Methods
  private listenOnSelectHour($event: Event): void {
    this.dataPointHour = Number((<HTMLInputElement>(<any>$event).target).value);
    this.emitSelectHour();
  }
  private listenOnSelectMinute($event: Event): void {
    this.dataPointMinute = Number((<HTMLInputElement>(<any>$event).target).value);
    this.emitSelectMinute();
  }
  private listenOnSelectSecond($event: Event): void {
    this.dataPointSecond = Number((<HTMLInputElement>(<any>$event).target).value);
    this.emitSelectSecond();
  }
  private initHours(): void {
    const HOUR_TERM: number = 24;
    for (let i: number = 0; i < HOUR_TERM; i++) {
      this.dataHours.push(i);
    }
  }
  private initMinutesAndSeconds(): void {
    const MINUTE_AND_SECOND_TERM: number = 60;
    for (let i: number = 0; i < MINUTE_AND_SECOND_TERM; i++) {
      this.dataMinutesAndSeconds.push(i);
    }
  }
}
