






























































































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import moment from 'moment';
import MomentUtil from '@/typescript/MomentUtil.ts';
import CDatetime from '@/components/links/CDatetime.vue';
import CSelectorYMD from '@/components/CSelectorYMD.vue';
import CSelectorHMS from '@/components/CSelectorHMS.vue';

@Component({
  components: {
    CDatetime,
    CSelectorYMD,
    CSelectorHMS,
  },
  head: {
    title: {
      inner: '日時差分計算',
    },
  },
})
export default class TimeDiff extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataYearTerms: {
    start: number,
    end: number,
  } = {
    start: -120,
    end: 120,
  };
  private dataStartPoint: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };
  private dataEndPoint: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };
  private dataEachPointDiff: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };
  private dataPointDiff: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };
  private dataAlert: string = '';

  // Watch
  @Watch('dataStartPoint')
  onStartPointChange(newValue: string, oldValue: string): void {
  }
  @Watch('dataEndPoint')
  onEndPointChange(newValue: string, oldValue: string): void {
  }

  // Lifecycle hook
  created(): void {
    let startDateMoment: any = moment(new Date()).startOf('years');
    let endDateMoment: any = startDateMoment.clone();
    this.dataStartPoint = MomentUtil.toDatePoint(startDateMoment);
    this.dataEndPoint = MomentUtil.toDatePoint(endDateMoment);
    this.diffDatePoints();
  }

  // Methods
  private diffDatePoints(): void {
    let startMoment: any = MomentUtil.toDateMoment(this.dataStartPoint);
    let endMoment: any = MomentUtil.toDateMoment(this.dataEndPoint);
    if (endMoment.diff(startMoment) < 0) {
      this.dataAlert = '開始日時を終了日時よりも前にしてください。';
    } else {
      this.dataAlert = '';
      this.dataEachPointDiff = MomentUtil.diffDateMomentsToEachPointDiff(startMoment, endMoment);
      this.dataPointDiff = MomentUtil.diffDateMomentsToTotalPointDiff(startMoment, endMoment);
    }
  }
  private listenOnStartPointYearChange($event: Event): void {
    this.dataStartPoint.year = Number($event);
    this.diffDatePoints();
  }
  private listenOnStartPointMonthChange($event: Event): void {
    this.dataStartPoint.month = Number($event);
    this.diffDatePoints();
  }
  private listenOnStartPointDayChange($event: Event): void {
    this.dataStartPoint.day = Number($event);
    this.diffDatePoints();
  }
  private listenOnStartPointHourChange($event: Event): void {
    this.dataStartPoint.hour = Number($event);
    this.diffDatePoints();
  }
  private listenOnStartPointMinuteChange($event: Event): void {
    this.dataStartPoint.minute = Number($event);
    this.diffDatePoints();
  }
  private listenOnStartPointSecondChange($event: Event): void {
    this.dataStartPoint.second = Number($event);
    this.diffDatePoints();
  }
  private listenOnEndPointYearChange($event: Event): void {
    this.dataEndPoint.year = Number($event);
    this.diffDatePoints();
  }
  private listenOnEndPointMonthChange($event: Event): void {
    this.dataEndPoint.month = Number($event);
    this.diffDatePoints();
  }
  private listenOnEndPointDayChange($event: Event): void {
    this.dataEndPoint.day = Number($event);
    this.diffDatePoints();
  }
  private listenOnEndPointHourChange($event: Event): void {
    this.dataEndPoint.hour = Number($event);
    this.diffDatePoints();
  }
  private listenOnEndPointMinuteChange($event: Event): void {
    this.dataEndPoint.minute = Number($event);
    this.diffDatePoints();
  }
  private listenOnEndPointSecondChange($event: Event): void {
    this.dataEndPoint.second = Number($event);
    this.diffDatePoints();
  }
}
